.avatar {
  user-select: none;
  text-align: center;
  border-radius: 50%;
  color: var(--ui-color-primary-inverted);
  background-color: var(--ui-color-primary);

  &_size {
    &_small {
      width: var(--ui-element-size-small);
      height: var(--ui-element-size-small);
      line-height: var(--ui-element-size-small);
      font-size: var(--ui-font-size-md);
    }

    &_medium {
      width: var(--ui-element-size-medium);
      height: var(--ui-element-size-medium);
      line-height: var(--ui-element-size-medium);
      font-size: var(--ui-font-size-lg);
    }

    &_large {
      width: var(--ui-element-size-large);
      height: var(--ui-element-size-large);
      line-height: var(--ui-element-size-large);
      font-size: var(--ui-font-size-heading-3);
    }
  }

  &__img {
    display: block;
    background: var(--ui-color-background-primary);
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
    -webkit-user-drag: none;
  }
}